a {
  text-decoration: none;
}

.navbar {
  background-color: #333333;
  display: flex;
  align-items: center;
}

.navbar a {
  color: #e7e7e7;
}

.navbar ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  align-items: center;
}

.navbar .brand {
  display: block;
  color: #e7e7e7;
  padding: 0 24px;
  font-size: 24px;
}

.navbar ul li a {
  color: #e7e7e7;
  padding: 24px;
  display: block;
}

.navbar li {
  border:#e7e7e7;
  border-radius: 2%;
}

.navbar ul a:hover ,
.navbar ul a:focus {
  background-color: #272727;
}
